import { useState } from 'react';
import {
  ServicesBlockContainer,
  ServicesBlockItem,
  ServicesBlockItemWithBorder,
  ServicesBlockSubTitle,
  ServicesChoose,
  ServicesImg,
  ServicesMainContainer,
  ServicesMainTitle,
  ServicesMainTitleContainer,
  ServicesMainTitleContainerChoose,
  ServicesSubTitle,
  ServicesSubTitleContainer,
  ServicesTitle,
  ServicesTopCon,
  ServicesTopImgContainer,
  ServicesTopImgContainerChoose,
  ToKnowImage,
  ToKnowMoreArrowBlock,
  ToKnowMoreBlock,
  ToKnowMoreText,
} from './Services.styled';
import headphones from '../../images/headphones.svg';
import longArrow from '../../images/longArrow.png'

const Services = () => {
  const [selectedIndexes, setSelectedIndexes] = useState([]); //   

  const data = [
    {
      img: {
        default: <ServicesImg color="black" alt='headphones' />,
        selected: <ServicesImg color="white" alt='headphones' />,
      },
      title: ' ',
      subTitle:
        '     ,    ,      .',
    },
    {
      img: headphones,
      title: ' ',
      subTitle:
        '      ,    ,      .',
    },
    {
      img: headphones,
      title: ' ',
      subTitle:
        '       ,  ,   ,     .',
    },
    {
      img: headphones,
      title: '  ',
      subTitle:
        '       ,  ,   ,     .',
    },
    {
      img: headphones,
      title: '   ',
      subTitle:
        '       ,  ,   ,     .',
    },
    {
      img: headphones,
      title: '  SEO',
      subTitle:
        '       ,  ,   ,     .',
    },
  ];

  const handleSelect = index => {
    //    ,    ,    
    if (selectedIndexes.includes(index)) {
      setSelectedIndexes(selectedIndexes.filter(i => i !== index)); //   
    } else {
      setSelectedIndexes([...selectedIndexes, index]); //   
    }
  };

  return (
    <ServicesMainContainer>
      <ServicesTitle>Sanity / Company</ServicesTitle>
      <ServicesMainTitle>   </ServicesMainTitle>
      <ServicesSubTitleContainer>
        <ServicesSubTitle>
               
        </ServicesSubTitle>
        <ServicesChoose>: {selectedIndexes.length}</ServicesChoose>
      </ServicesSubTitleContainer>
      <ServicesBlockContainer>
        {data?.map((item, index) => {
          const isSelected = selectedIndexes.includes(index); // ,   

          return isSelected ? (
            <ServicesBlockItemWithBorder
              key={index}
              onClick={() => handleSelect(index)}
            >
              <ServicesTopCon>
                <ServicesTopImgContainerChoose>
                  <ServicesImg color="white" alt={item.title} />
                </ServicesTopImgContainerChoose>
                <ServicesMainTitleContainerChoose>
                  {item.title}
                </ServicesMainTitleContainerChoose>
              </ServicesTopCon>
              <ServicesBlockSubTitle>{item.subTitle}</ServicesBlockSubTitle>
            </ServicesBlockItemWithBorder>
          ) : (
            <ServicesBlockItem
              key={index}
              onClick={() => handleSelect(index)} //  
            >
              <ServicesTopCon>
                <ServicesTopImgContainer>
                  <ServicesImg color="black" alt={item.title} />
                </ServicesTopImgContainer>
                <ServicesMainTitleContainer>
                  {item.title}
                </ServicesMainTitleContainer>
              </ServicesTopCon>
              <ServicesBlockSubTitle>{item.subTitle}</ServicesBlockSubTitle>
            </ServicesBlockItem>
          );
        })}
      </ServicesBlockContainer>
      <ToKnowMoreBlock>
        <ToKnowMoreText> </ToKnowMoreText>
        <ToKnowMoreArrowBlock><ToKnowImage src={longArrow} alt='longArrow' /></ToKnowMoreArrowBlock>
      </ToKnowMoreBlock>
    </ServicesMainContainer>
  );
};

export default Services;
